<template>
    <v-container fluid>
        <div v-if="!loading" class="body-2">
            <v-row class="my-2 subtitle-2 secondary--text pl-12">
                Users
            </v-row>
            <v-row class="subtitle-2" no-gutters>
                <v-col v-for="(item, index) in headers" :key="index">{{item}}</v-col>
            </v-row>
            <v-row v-for="user in users" :key="user.id" no-gutters>
                <v-col>{{ user.name }}</v-col>
                <v-col>{{ user.email }}</v-col>
                <v-col>{{ user.role }}</v-col>
                <v-col>{{ dateToString(user.createdAt) }}</v-col>
                <v-col>
                    <!-- <update :user="user" /> -->
                </v-col>
            </v-row>
        </div>
        <div v-else>
            <loader></loader>
        </div>
    </v-container>
</template>

<script>
import Loader from '../../../layouts/Loader.vue'
import { ConvertDateToString } from '../../../functions/common';

export default {
    name: 'Users',
    data() {
        return {
            headers: ['Name', 'Email', 'Role', 'reation date', 'Action'],
        }
    },
    computed: {
        users() {
            console.log(this.$store.getters.users)
            return this.$store.getters.users;
        },
        loading() {
            return this.$store.getters.loading;
        },
        locale() {
            return this.$store.getters.locale;
        },
        loggedIn() {
            return this.$store.getters.loggedIn
        }
    },
    watch: {
        loggedIn() {
            if (!this.loggedIn) {
                this.$router.push('/home');
            }
        }
    },
    methods: {
        dateToString(date) {
            return ConvertDateToString(date, this.locale);
        }
    },
    components: {
        Loader,
    },
    async created() {
        try {
           await this.$store.dispatch('fetchAllUsers'); 
        }
        catch(error) {
            console.log(error.message)
        }  
    }
}
</script>
